import React, { useState } from "react";
import "./styles.css";

// var likeCounter = 0;

export default function App() {
  const [meaning, setmeaning] = useState("");

  var EmojiDictionary = {
    "üòä": "Smiling Face with Smiling Eyes",
    "ü•∞": "Hearts",
    "üòç": "Smiling Face with Heart-Eyes",
    "ü§©": "Star-Struck",
    "üòò": "Face Blowing a Kiss",
    "üòó": "Kissing Face",
    "üòö": "Kissing Face with Closed Eyes",
    "üòô": "Kissing Face with Smiling Eyes"
  };

  function eventHandler(event) {
    var userInput = event.target.value;

    var meaning = EmojiDictionary[userInput];

    if (userInput in EmojiDictionary) {
      setmeaning(meaning);
    } else {
      setmeaning(" Sorry We Don't have that in our database");
    }
  }
  var emojiweknow = Object.keys(EmojiDictionary);

  //part 2
  function emojiClickedhandler(emoji) {
    var meaning = EmojiDictionary[emoji];
    setmeaning(meaning);
  }

  return (
    <div className="App">
      <h1>Inside Out</h1>

      <input onChange={eventHandler}></input>
      <div className="meaning">{meaning} </div>

      <h2>Emoji's we know</h2>

      {emojiweknow.map(function (emoji) {
        //instead using for loops use map
        return (
          <span
            onClick={() => emojiClickedhandler(emoji)}
            key={emoji}
            style={{ padding: "5px", fontSize: "2rem", cursor: "pointer" }}
          >
            {emoji}
          </span>
        );
      })}
    </div>
  );
}
